import axios from 'axios'
import * as qs from 'qs'

import { BASE_URL, errorHandler, TIMEOUT } from './config'

const instance = axios.create({
  baseURL: BASE_URL,
  timeout: TIMEOUT
})

// ËØ∑Ê±ÇÊã¶Êà™
// qs Â∫èÂàóÂåñ Èò≤Ê≠¢XSRFÊîªÂáª ÂèØ‰ª•ÂØπÊ∑±Â±ÇÊ¨°ÁöÑjson arrayËøõË°åÂ∫èÂàóÂåñ
instance.interceptors.request.use(
  (config: any) => {
    // if (config.url.includes('?')) {
    //   config.url += `&t=${new Date().getTime()}`
    // } else {
    //   config.url += `?t=${new Date().getTime()}`
    // }
    // `transformRequest` ÂÖÅËÆ∏Âú®ÂêëÊúçÂä°Âô®ÂèëÈÄÅÂâçÔºå‰øÆÊîπËØ∑Ê±ÇÊï∞ÊçÆ
    // Âè™ËÉΩÁî®Âú® 'PUT', 'POST' Âíå 'PATCH' ËøôÂá†‰∏™ËØ∑Ê±ÇÊñπÊ≥ï
    // ÂêéÈù¢Êï∞ÁªÑ‰∏≠ÁöÑÂáΩÊï∞ÂøÖÈ°ªËøîÂõû‰∏Ä‰∏™Â≠óÁ¨¶‰∏≤ÔºåÊàñ ArrayBufferÔºåÊàñ Stream
    config.transformRequest = [
      (data, headers) => {
        return qs.stringify(data, {
          allowDots: true
        })
      }
    ]
    // `paramsSerializer` ÊòØ‰∏Ä‰∏™Ë¥üË¥£ `params` Â∫èÂàóÂåñÁöÑÂáΩÊï∞
    config.paramsSerializer = (params) => {
      return qs.stringify(params, {
        arrayFormat: 'repeat'
      })
    }
    return config
  },
  async (error) => {
    return await Promise.reject(error)
  }
)

// ÂìçÂ∫îÊã¶Êà™
instance.interceptors.response.use(
  (res) => {
    return res.data
  },
  (err) => {
    if (err) {
      errorHandler(err)
    }
    return err
  }
)

export default instance
