import { Button, Form, Input } from 'antd'
import React, { useEffect, useRef, useState } from 'react'

import { captchaAccess } from '@/service/api'
import { formType } from '@/typings/'

const timerCount = 60 // ÈªòËÆ§60Áßí
const VerificationCode = (props: formType) => {
  const [count, setCount] = useState(timerCount)

  const timerRef = useRef(null) // ËÆ∞ÂΩïÊó∂Èó¥ÁöÑÂÆöÊó∂Âô®

  const { phone } = props

  const cutCount = () => {
    setCount((prevState) => prevState - 1) // ‰∏∫‰ªÄ‰πàËøôÈáåË¶ÅÁî®ÂáΩÊï∞- Â¶ÇÊûúÁî®count ÂèëÁé∞countÈó≠ÂåÖ‰∫Ü ‰∏ç‰ºöÂèëÁîüÂèòÂåñ‰∫Ü
  }

  // ËØ∑Ê±ÇÈ™åËØÅÁ†Å
  const getCaptha = async function() {
    return await captchaAccess({ phone })
  }

  const sendCode = async () => {
    // Ë¶ÅÂèëÈÄÅÈ™åËØÅÁ†Å
    getCaptha()
    cutCount()
    timerRef.current = setInterval(cutCount, 1000)
  }

  useEffect(() => {
    if (count === 0) {
      clearInterval(timerRef.current) // Ê∏ÖÁ©∫ÂÆöÊó∂Âô®
      setCount(timerCount) // ÈáçÊñ∞Â∞ÜÊäÄÊúØÂô®ËÆæÁΩÆ‰∏∫60Áßí
    }
  }, [count])

  return (
    <>
      <Button
        disabled={count < timerCount}
        onClick={count === timerCount ? sendCode : null}
      >
        {count === timerCount ? 'ÂèëÈÄÅÈ™åËØÅÁ†Å' : `ËøòÂâ©${count}Áßí`}
      </Button>
      <Form.Item noStyle name="captcha" >
        <Input type='number' />
      </Form.Item>
    </>
  )
}

export default VerificationCode
